// Esperar a que el DOM estÃ© completamente cargado
document.addEventListener("DOMContentLoaded", function () {
    // Valores para cambiar el texto
    var valores = ["1.00", "0.50", "0.25"];
    var coloresTexto = {
        "1.00": "#30475E", // Azul oscuro
        "0.50": "#E08709", // Naranja
        "0.25": "#F05454"  // Rojo claro
    };

    // DelegaciÃ³n de eventos en el contenedor padre
    document.addEventListener("click", function (event) {
        // Verificar si el clic ocurriÃ³ en un botÃ³n con la clase 'unidad-boton'
        if (event.target.closest(".unidad-boton")) {
            var boton = event.target.closest(".unidad-boton"); // El botÃ³n especÃ­fico clicado
            var texto = boton.querySelector("#unidad-texto"); // PÃ¡rrafo interno del botÃ³n

            // Verificar y establecer el Ã­ndice del botÃ³n
            var indice = parseInt(boton.dataset.indice || 0, 10);
            indice = (indice + 1) % valores.length; // Incrementar el Ã­ndice cÃ­clicamente
            boton.dataset.indice = indice; // Guardar el Ã­ndice actualizado en el botÃ³n

            // Cambiar el texto y el color
            texto.innerHTML = valores[indice];
            texto.style.color = coloresTexto[valores[indice]];
        }
    });

    // Inicializar el estado para todos los botones al cargar la pÃ¡gina
    document.querySelectorAll(".unidad-boton").forEach(function (boton) {
        var texto = boton.querySelector("#unidad-texto"); // PÃ¡rrafo interno
        var valorActual = texto.innerHTML.trim(); // Obtener el valor actual del texto
        var indiceActual = valores.indexOf(valorActual); // Buscar el Ã­ndice del valor actual

        if (indiceActual !== -1) {
            texto.style.color = coloresTexto[valorActual]; // Aplicar el color correspondiente
            boton.dataset.indice = indiceActual; // Sincronizar el Ã­ndice con el valor actual
        } else {
            boton.dataset.indice = 0; // Si no se encuentra el valor, usar el primer Ã­ndice
        }
    });
});
